.navbar {
    width: 100%;
    height: 100px;
    background-color: #110a2f;
    display: flex;
    flex-direction: row;
    padding: 0 20px; /* Sayfan覺n kenarlar覺na yap覺may覺 engellemek i癟in padding ekledik */
    box-sizing: border-box; /* Padding'in genilii etkilememesi i癟in */
}

.navbar .leftSide {
    flex: 50%;
    display: flex;
    align-items: center;
    height: 100%;
    padding-left: 20px; /* Soldaki resmin kenara yap覺mas覺n覺 engellemek i癟in */
    gap: 15px; /* Logo ile yaz覺 aras覺nda boluk b覺rakmak i癟in */
}

.navbar .leftSide .siteName {
    font-size: 20px; /* Yaz覺 boyutu */
    font-weight: bold; /* Kal覺n yaz覺 */
    color: white; /* Yaz覺 rengi */
    text-transform: uppercase; /* B羹y羹k harf */
    letter-spacing: 1px; /* Harfler aras覺nda hafif boluk */
}
.navbar .leftSide img {
    width: 70px;
    border-radius: 50%; /* Resmi yuvarlak yapmak i癟in */
}

.navbar .rightSide {
    flex: 50%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 100%;
    padding-right: 20px; /* Sadaki yaz覺lar覺n kenara yap覺mas覺n覺 engellemek i癟in */
    gap: 15px; /* Yaz覺lar aras覺nda boluk b覺rakmak i癟in */
}

.navbar .rightSide a {
    text-decoration: none;
    color: white;
    background-color: #1e1e3f;
    padding: 10px 20px; /* Oval ekil i癟in padding */
    border-radius: 20px; /* Oval ekil i癟in border-radius */
    font-size: 16px;
    transition: background-color 0.3s ease; /* Hover efekti i癟in ge癟i */
}

.navbar .rightSide a:hover {
    background-color: #2a2a5f; /* Hover efekti */
}


/* A癟覺l覺r men羹 */
.dropdown {
    position: absolute;
    top: 50px;
    right: 10px;
    background-color: #333;
    border: 1px solid #444;
    border-radius: 5px;
    padding: 10px;
    display: flex;
    flex-direction: column; /* Linkleri alt alta s覺ralar */
    gap: 10px; /* Linkler aras覺nda boluk b覺rak覺r */
    z-index: 1000;
  }
  
  /* Men羹deki linkler */
  .dropdown a {
    color: white;
    text-decoration: none;
    padding: 5px 10px;
    border-radius: 3px;
    transition: background-color 0.3s;
  }
  
  .dropdown a:hover {
    background-color: #555;
  }

  .logout-button {
    background-color: #f5b6e9;
    color: white;
    padding: 8px 12px;
    border: none;
    border-radius: 5px;
    margin-left: 10px;
    cursor: pointer;
    font-size: 14px;
  }
  
  .logout-button:hover {
    background-color: #ce69c0;
  }
  
